<template>
  <div v-if="isLoading" class="loading">
    <div class="loading">
      <img
        class="loading-img"
        src="../../assets/img/loading.gif"
      />
    </div>
  </div>
  <div class="book" v-else>
    <swiper :options="swiperOption" class="book-show">
      <swiper-slide v-for="item in bookList" :key="item.id" @click.native="goDetail(item.id)">
        <div>
          <img :src="item.cover.url">
          <div>{{item.title}}</div>
        </div>
      </swiper-slide>
      <div class="swiper-pagination" slot="pagination"></div>
    </swiper>
    <table cellspacing="0">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item,index) in bookList" :key="index" @click="goDetail(item.id)">
          <td class="num" align="center">{{index+1}}</td>
          <td width="47%" align="center">{{item.title}}</td>
          <td align="center">{{item.author[0]}}</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
import "swiper/dist/css/swiper.css";

import { swiper, swiperSlide } from "vue-awesome-swiper";
import axios from 'axios'
export default {
  components: {
    swiper,
    swiperSlide
  },
  created() {
    let obj = {
      title: "",
      className: "book"
    };
    this.$emit("changeNav", obj);
    this.getData();
  },
  data() {
    return {
      swiperOption: {
        slidesPerView: 3,
        spaceBetween: 30,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      bookList: [],
      isLoading: true
    };
  },
  methods: {
    getData(){
      let url = 'https://bird.ioliu.cn/v2?url=https://m.douban.com/rexxar/api/v2/subject_collection/book_fiction/items?start=0&count=20'
      axios.get(url)
      .then((res) => {
        this.bookList = res.data.subject_collection_items;
        this.isLoading = false;
      })
      .catch(() => {
        return false;
      })
    },
    goDetail(id){
      this.$router.push({name:'detailpage',params: {id}})
    }
  },
};
</script>
<style scoped>
*{
  margin: 0;
  padding: 0;
}
.book{
  width: 100%;
  font-size: .3rem
}
.swiper-slide{
  width: 30% !important;
  margin-right: 4% !important;
  text-align: center;
  padding: .5rem 0;
}
.swiper-slide img{
  width: 100%;
  height: 150px;
}
.swiper-wrapper{
  width: 100%;
}
.book-show{
  background-color: rgb(167, 250, 232);
  border-bottom: 1px solid #333;
}
.num{
  font-size: .1rem;
}
table th {
  font-size: 0.3rem;
  color: #f37f07;
}
tbody tr:nth-child(odd) {
  background-color: #f3eb91ed;
}
.loading {
  width: 100vw;
  height: 83vh;
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  top: 1rem;
}
.loading-img {
  padding: 0;
  width: 200px;
  height: 100px;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
</style>
